"use client";

import { useEffect, useState } from "react";
import Link from "next/link";
import { PortableText } from "@portabletext/react";
import BlogHeader from "@/components/BlogHeader";
import Footer from "@/components/footer";
import { Linkedin, Link as LinkIcon } from "lucide-react";
import { portableTextComponents } from "@/lib/portableTextComponents";

export default function BlogPostClient({ post, relatedPosts }) {
  const [progress, setProgress] = useState(0);
  const [pageUrl, setPageUrl] = useState("");
  const [linkCopied, setLinkCopied] = useState(false);

  useEffect(() => {
    setPageUrl(window.location.href);
  }, []);

  useEffect(() => {
    const onScroll = () => {
      const total =
        document.documentElement.scrollHeight -
        document.documentElement.clientHeight;

      setProgress((window.scrollY / total) * 100);
    };

    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <>
      <BlogHeader />

      <div className="fixed top-0 left-0 w-full h-1 bg-gray-200 z-50">
        <div
          className="h-full bg-blue-600"
          style={{ width: `${progress}%` }}
        />
      </div>

      {/* sidebar */}
      <div className="hidden lg:flex fixed right-6 top-1/3 flex-col gap-3 z-40">
        <button
          onClick={async () => {
            await navigator.clipboard.writeText(pageUrl);
            setLinkCopied(true);
            setTimeout(() => setLinkCopied(false), 2000);
          }}
          className={`w-10 h-10 rounded-full border flex items-center justify-center
            ${linkCopied ? "bg-green-600 text-white" : "bg-white"}`}
        >
          <LinkIcon size={18} />
        </button>

        <a
          href={`https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(
            pageUrl
          )}`}
          target="_blank"
          rel="noopener noreferrer"
          className="w-10 h-10 rounded-full border bg-white flex items-center justify-center text-[#0A66C2]"
        >
          <Linkedin size={18} />
        </a>
      </div>

      <main className="max-w-4xl mx-auto px-5 py-20">
        <Link href="/blog" className="text-blue-600 text-sm mb-6 inline-block">
          ← Back to Blog
        </Link>

        <h1 className="text-4xl font-bold mb-6">{post.title}</h1>

        <article className="prose prose-lg max-w-none">
          <PortableText
            value={post.content}
            components={portableTextComponents}
          />
        </article>

        {relatedPosts?.length > 0 && (
          <section className="mt-24">
            <h2 className="text-2xl font-bold mb-8">Related articles</h2>
            <div className="grid md:grid-cols-3 gap-8">
              {relatedPosts.map((rp) => (
                <Link
                  key={rp._id}
                  href={`/blog/${rp.slug}`}
                  className="border rounded-xl overflow-hidden hover:shadow-lg"
                >
                  <img
                    src={rp.mainImage?.asset?.url}
                    alt={rp.title}
                    className="h-44 w-full object-cover"
                  />
                  <div className="p-5">
                    <h3 className="font-semibold">{rp.title}</h3>
                  </div>
                </Link>
              ))}
            </div>
          </section>
        )}
      </main>

      <Footer />
    </>
  );
}
